.header__logo {
  height: 4rem;
  width: 4rem;
}

.btn__profile:focus + .header__profile-menu {
  opacity: 1;
  visibility: visible;
}

.btn-secondary {
  border-radius: 5px;
  padding: 0.9rem 2rem;
  color: var(--color-primary);
  background-color: var(--color-tertiary);
}
.btn-secondary:not(:last-of-type) {
  margin-bottom: 2rem;
}
.btn-secondary:hover {
  color: var(--color-tertiary);
}

.header__profile-menu {
  padding: 2.8rem 5rem;
  border-radius: 2px;
  position: absolute;
  z-index: 10;
  right: -3.5rem;
  top: 4.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s 0.2s;
  background-color: var(--color-primary);
  border: 1px solid var(--color-border);
}

.header__nav {
  display: flex;
  position: relative;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 2rem 15rem;
  background-color: var(--color-primary);
  border-bottom: 1px solid var(--color-border);
}

.figure {
  padding: 2rem 3rem;
  border-radius: 3px;
  background-color: #f7f7f7;
}
.figure:not(:last-of-type) {
  margin-right: 4rem;
}
.figure__link {
  text-decoration: none;
}
.figure__image {
  height: 20rem;
}
.figure__title {
  font-size: 2rem;
}
.figure__price {
  font-size: 1.5rem;
}

.figure-container {
  display: flex;
}
.figure-container:not(:last-of-type) {
  margin-bottom: 5rem;
}

.heading-default {
  font-family: inherit;
  font-weight: 300;
  color: var(--color-secondary);
}
.heading-primary {
  font-size: 3rem;
  margin-bottom: 2rem;
}

.main {
  padding: 5rem 10rem;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(39, 39, 39, 0.9);
  z-index: 100;
  transition: all 0.2s;
  visibility: hidden;
  opacity: 0;
}

:root {
  --color-light: #ffff;
  --color-primary: #f7f7f7;
  --color-secondary: rgb(82, 77, 77);
  --color-tertiary: #ff85a1;
  --color-border: rgba(221, 221, 221, 0.595);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 65.5%;
}

body {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  line-height: 1.6;
  position: relative;
}

.container {
  height: 100vh;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr;
  grid-template-rows: min-content 1fr;
}

.btn {
  background: none;
  border: none;
  font-family: inherit;
  font-size: 1.5rem;
  cursor: pointer;
  border-radius: 3px;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  transition: all 0.5s;
  padding: 0.9rem 2rem;
}
.btn:hover {
  background-color: #ffff;
  background-color: var(--color-light);
}
.btn__icon {
  height: 2.2rem;
  width: 2.2rem;
  margin-left: 1rem;
  fill: #ff85a1;
  fill: var(--color-tertiary);
}

.btn-primary {
  color: #ff85a1;
  color: var(--color-tertiary);
}

.btn-secondary {
  border-radius: 5px;
  padding: 0.9rem 2rem;
  color: #f7f7f7;
  color: var(--color-primary);
  background-color: #ff85a1;
  background-color: var(--color-tertiary);
}

